.CreatePage {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.CreatePage__Main {
    padding-top: 50px;
    padding-right: 10px;
    padding-bottom: 50px;
    padding-left: 10px;
    text-align: center;
}

.CreatePage__Main h3 {
    font-size: 24px;
}

.CreatePage__Main button {
    width: 100%;
    height: 33px; 
}

.RecipeTitle {
    text-align: left;
}

.RecipeTitle input {
    font-size: 24px;
    font-weight: bold;
    width: 100%;
    border: solid black 1px;
    border-radius: 3px;
}

.RecipeTitle label {
    font-size: 18px;
}

.Recipe__Box {
    padding: 20px;
    border: black solid 1px;
    border-radius: 3px;
}

.Recipe__Box h3 {
    font-size: 18px;
}

.Recipe__Box button {
    width: 33px;
}

.Recipe__Item {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.Recipe__Item button {
    height: 20px;
}

.Buttons__Box {
    padding-top: 20px;
    padding-bottom: 20px;
}

.Ingredients__List {
    border: black solid 1px;
    border-radius: 3px;
}

.Ingredients__Item span {
    padding: 5px;
}

.add {
    width: 38px !important;
    height: 20px !important;
}

.accordionForm {
    padding-top: 3px;
    padding-bottom: 3px;
    text-align: center;
}

.newIngredientForm label {
    padding-right: 3px;
}

.thanksBox {
    background-color: rgb(245, 245, 245);
    border-radius: 3px;
    padding: 33px;
}

.thanksBox h2 {
    font-size: 33px;
}

.thanksBox h3 {
    font-size: 20px;
}

a {
    text-decoration: none;
    color: rgb(0, 82, 0);
}